.primer-div {
    color: white;
    background-color: black;
    display: flex;
    justify-content: space-around;
    align-items: stretch;
    min-height: 100vh;
    width: 100vw;

    @media (max-width: 768px) {
        flex-direction: column;
        align-items: center;
    }
}

.home-title {
    width: 45%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 0 0 180px;
    flex-direction: column;
    

    @media (max-width: 768px) {
        width: 90%;
        font-size: 30pt;
        text-align: center;
        padding: 2% 20%;
    }
    
    @media (max-width: 480px) {
        font-size: 20pt;
        padding: 7% 10% 0 10%;
    }
}

.card-3d {
    height: 80vh;
    width: 45%;

    @media (max-width: 768px) {
        width: 90%;
        height: 70vh;
    }
    @media (max-width: 480px) {
        height: 60vh;
    }
}

.segundo-div {
    background-color: black;
    color: white;
    display: flex;
    min-height: 80vh;
    align-items: center;

    background-image: url('../assets/background4.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

}

.segundo-div-primer-item {
    width: 100%;
    min-height: 50vh;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
}

.home-item {
    font-size: 30pt;
}

.ficha-home {
    background-color: rgba(0, 0, 0, 0.8);
    width: 250px;
    height: 350px;
    border-radius: 15px;
    color: orangered;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px;
    text-align: center;
    transition: transform 0.3s ease-in-out;

    @media (max-width: 480px) {
        max-width: 80vw;
        margin: 5px;
    } 
}
.ficha-home:hover {
    transform: rotate(-5deg);
    background-color: rgba(25, 25, 25, 0.8);
}

