.borde-rojo {
    border: 5px solid red;
}
.borde-azul {
    border: 5px solid blue;
}
.borde-verde {
    border: 5px solid green;
}

.columna-centrado {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
}

@font-face {
    font-family: 'MiFuente';
    src: url('../assets/NorseBold-2Kge.otf') format('opentype');
}

h1 {
    font-family: MiFuente;
    font-size: 50pt;

    @media (max-width: 768px) {
        font-size: 30pt;
    }
    
    @media (max-width: 480px) {
        font-size: 20pt;
    }
}

h2 {
    font-family: MiFuente;
    font-size: 30pt;

    @media (max-width: 768px) {
        font-size: 22pt;
    }
    
    @media (max-width: 480px) {
        font-size: 18pt;
    }
}

h3 {
    font-family: MiFuente;
    font-size: 18pt;

    @media (max-width: 480px) {
        font-size: 13pt;
    }
}

.button-grande {
    font-family: MiFuente;
    font-size: 25pt;
    padding: 30px 60px;
    text-align: center;
    display: flex;
    align-items: center;

    @media (max-width: 768px) {
        font-size: 18pt;
        padding: 20px 40px;
    }
    
    @media (max-width: 480px) {
        font-size: 15pt;
        padding: 15px 30px;
    }
}