.the-header {
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    padding: 0 10%;

    @media (max-width: 768px) {
        height: 60px;
    }
    
    @media (max-width: 480px) {
        height: 40px;
    }
}

.logo-image {
    width: 220px;
    padding-top: 15px;

    @media (max-width: 768px) {
        width: 130px;
        padding-top: 20px;
    }
    
    @media (max-width: 480px) {
        width: 90px;
        padding-top: 25px;
    }
}

.button-connect {
    color: white;
    font-family: MiFuente;
    font-size: 13pt;
    padding: 15px 30px;
    text-align: center;
    display: flex;
    align-items: center;

    @media (max-width: 768px) {
        
    }
    
    @media (max-width: 480px) {
        font-size: 10pt;
        padding: 5px 15px;
    }
}