.sider-button {
    margin-bottom: 5px;
    background-color: black;

    @media (max-width: 768px) {
        width: 45px;
    }
}
.sider {
    position: fixed;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0);

}
.menu {
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 10px;
    font-family: MiFuente;
    font-size: 12pt;

    &.menu-collapsed {
        // Estilos para el menú retraído
        width: 70px;

        @media (max-width: 768px) {
            width: 45px;
        }
    }
}
